div{
  text-align: center;
}

td{
  font-size: medium;
}
.App {
  text-align: center;
  
}
.table{
  margin: auto;
  border-radius: 5%;
  border: 5px solid #ffffff;
}

.form_container {
  margin: auto;
  margin-top: 200px;
  width: 50%;
  border-radius: 5%;
  border: 5px solid #ffffff;
  background-image: url("./bck.jpg");
}


.input_box {
  
  position: relative;
  margin: 10px auto;
  width: 40%;
  height: 40px;
  
}

.input_box input {
  height: 100%;
  width: 100%;
  border: none;
  outline: none;
  padding: 0 30px;
  color: #333;
  transition: all 0.2s ease;
  
}
.input_box input:focus {
  border-color: #7d2ae8;
}